.header {
    color: #fff;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.punkLogo {
    width: 200px;
    cursor: pointer;
}

.header > div {
    margin: 10px;
}

.searchBar {
    background-color: #1c1c1e;
    height: 50px;
    flex: 1;
    display: flex;
    border-radius: 50px;
    align-items: center;
}

.searchIconContainer {
    margin: 10px;
}

.searchInput {
    background-color: transparent;
    border: none;
    width: 100%;
    outline: none;
    color: #eee;
}

.headerItems{
    display: flex;
    color: #a1a5b0;
}

.headerItems > p {
    margin: 10px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

.headerItems > p:hover {
    color: #59f9b7;
}

.headerActions {
    display: flex;
    align-items: center;
}

.headerActions > div {
    margin: 10px;
}

.themeSwitchContainer {
    display: flex;
    background-color: #1c1c1e;
    border-radius: 50px;
    padding: 15px;
    justify-content: center;
    align-items: center;
    object-fit: contain;
    transition: all 0.3s ease-in-out;
}

.themeSwitchContainer > img {
    height: 25px;
    cursor: pointer;
}

.themeSwitchContainer:hover {
   background-color: #59f9b7;
}

.loginBtn {
    background: linear-gradient(to right, #59f9b7, #66feea);
    padding: 15px 40px;
    border-radius: 50px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

.loginBtn:hover {
    background: linear-gradient(to right, #66feea, #59f9b7);
}